.Footer {
  position: fixed;
  left: 0;
  bottom: -1px;

  display: flex;
  justify-content: space-around;
  align-items: center;

  width: 100%;
  height: 40px;

  background: linear-gradient(90deg, rgba(128,91,249,1) 0%, rgba(193,131,236,1) 100%);
  color: #a7a7a7;

  box-shadow: 0 -3px 10px black;
  z-index: 1; }

.Footer--text {
  font-size: .6rem;
  color: #e8d4f7; }

.Footer--status {
  display: flex;
  align-items: center; }

.Footer--status p:nth-child(1) {
  color: #480577;

  font-weight: bold;
  font-size: .7rem; }

.Footer--status p:nth-child(2) {
  color: #f1f1f1;

  font-size: 1.8rem;
  font-weight: bold;
  text-shadow: 1px 2px 2px rgba(0, 0, 0, .75);

  transform: translate(2px, -2px); }
