@import url(https://fonts.googleapis.com/css2?family=Quantico:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
.Header{background-color:#181818;width:100%;height:10px;box-shadow:0 4px 10px black;box-sizing:content-box}header{display:flex;justify-content:center;align-items:center;transform:translateY(20px)}header h3{font-size:.9rem;text-shadow:3px 4px 3px #000}header h2{font-size:3.2rem;text-shadow:2px 3px 2px rgba(0,0,0,0.85);transform:translate(-12px, 2px);animation:changeColor 5s ease-in-out infinite alternate}

.GameList__item{display:flex;flex-direction:row;justify-content:space-between;margin:10px 0;width:100%;box-shadow:-2px 5px 6px .2px #0000003a;text-align:center;z-index:0}.GameList__item h3{text-shadow:1px 3px 2px #000}.GameList__item--naming img{border-radius:2px;background-color:#bc69f7;box-shadow:0 0 5px 2px rgba(0,0,0,0.8);height:50px;width:150px;object-fit:cover;opacity:.6}.GameList__item--naming img:hover,.GameList__item--naming img:focus{opacity:1}.GameList__item--naming img:active{-webkit-filter:initial;filter:initial;opacity:1;-webkit-filter:sepia(39%) saturate(523%) hue-rotate(340deg) brightness(85%) contrast(94%);filter:sepia(39%) saturate(523%) hue-rotate(340deg) brightness(85%) contrast(94%)}.GameList__btn{margin:30px 0 15px 0;padding:5px 20px;border-radius:4px;color:#181818}.ex--price{font-size:.8rem;text-decoration:line-through;opacity:.5}.actual--price{margin:-6px 0;font-size:1.6rem}.actual--price::after{content:'€';font-size:.6rem}.GameList__item--naming{text-align:left}.GameList__item--info{display:flex;flex-direction:column;align-self:center;padding:0 10px}.GameList__item--anim{position:relative;left:0;display:flex;align-items:center;width:100%;transform:translate(22px, 26px);opacity:.75}.shape img{width:16px;height:16px;box-shadow:0 0 0}

.GameList{display:flex;flex-direction:column;align-items:center;margin:60px 0;animation:fadeIn 1s}

.Search{top:0;bottom:0;display:flex;flex-direction:column;align-items:center;background:#181818;margin:60px 0;width:100%;animation:.5s fadeIn;z-index:2}input{position:relative;margin:0 10px 0 0;padding:3px 10px;border-radius:3px;background-color:#484848;color:#f1f1f1;font-size:.75rem;z-index:9}.Search button{padding:2px 20px;background:linear-gradient(90deg, #805bf9 0%, #c183ec 100%);color:white;border-radius:3px;font-weight:bold;font-size:.8rem;cursor:pointer;opacity:.5;transition:all 1s;animation:changeColorBtn 1.5s ease-in-out infinite alternate}.Search button:hover{background:linear-gradient(90deg, #3c00ff 0%, #9500ff 100%);opacity:1}.Search button:focus,.Search button:active{box-shadow:inset 0 0 6px black}.Search .GameList{margin:0;animation:fadeIn 1s}.Search .GameList__item--anim{display:none}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}

.Loader{position:fixed;top:0;left:0;width:100%;height:100vh;background:rgba(0,0,0,0.75);z-index:1}.Loader__anim{display:flex;justify-content:center;align-items:center;width:100%;height:100%}.Loader__anim h2{color:#bc69f7;font-size:1.5rem;letter-spacing:5px;transform:translateY(-50px)}@media (min-width: 676px){.Loader__anim h2{font-size:2.5rem}}

.Footer{position:fixed;left:0;bottom:-1px;display:flex;justify-content:space-around;align-items:center;width:100%;height:40px;background:linear-gradient(90deg, #805bf9 0%, #c183ec 100%);color:#a7a7a7;box-shadow:0 -3px 10px black;z-index:1}.Footer--text{font-size:.6rem;color:#e8d4f7}.Footer--status{display:flex;align-items:center}.Footer--status p:nth-child(1){color:#480577;font-weight:bold;font-size:.7rem}.Footer--status p:nth-child(2){color:#f1f1f1;font-size:1.8rem;font-weight:bold;text-shadow:1px 2px 2px rgba(0,0,0,0.75);transform:translate(2px, -2px)}

.navButtonLeft,.navButtonRight{position:fixed;top:calc(50vh - 50px);right:-40px;width:80px;height:70px;margin:0;padding-right:35px;padding-bottom:12px;font-size:1.6rem;font-weight:lighter;font-family:'Reggae One', cursive;border-radius:40px;background:rgba(37,37,37,0.856);color:#bc69f7;opacity:.75}.navButtonLeft{left:-40px;padding-right:18px;padding-bottom:12px;text-align:right}.navButtonLeft:hover,.navButtonRight:hover{opacity:1}.searchExpanderBtn{position:fixed;top:50px;right:-16px;padding:0 10px 0 0;width:60px;height:45px;background-color:#484848;border-radius:35px;color:#f1f1f1;font-size:1rem;box-shadow:0 0 5px 4px rgba(0,0,0,0.418);opacity:.75;z-index:3}

.SplashScreen{position:fixed;top:0;right:0;bottom:0;left:0;display:flex;flex-direction:column;align-items:center;justify-content:center;background:#000;text-align:center;opacity:1;transition:all .35s;z-index:4}.SplashScreen--way{position:absolute;top:50px;padding:0 15px}.SplashScreen--way p{font-size:.8rem;color:rgba(255,255,255,0.541)}.SplashScreen--title{margin:10px 0;width:300px;background:white;color:#202020;font-size:1.5rem;text-transform:uppercase}.SplashScreen--text p{font-size:.85rem;padding:0 50px}.SplashScreen hr{margin:15px 0;border-bottom:1px solid rgba(255,255,255,0.164)}.SplashScreen strong{color:#c593ff;font-size:.85rem;animation:changeColor 1s ease-in-out infinite alternate}.SplashScreen button{padding:5px 26px;background:linear-gradient(90deg, #805bf9 0%, #c183ec 100%);color:white;border-radius:3px;font-weight:bold;font-size:1rem;cursor:pointer;opacity:.75;transition:all 1s;animation:changeColorBtn 1.5s ease-in-out infinite alternate}.SplashScreen button:hover{background:linear-gradient(90deg, #3c00ff 0%, #9500ff 100%);opacity:1}.SplashScreen button:focus,.SplashScreen button:active{box-shadow:inset 0 0 6px black}@keyframes changeColor{from{color:blueviolet}to{color:violet}}@keyframes changeColorBtn{from{background:violet}to{background:blueviolet}}

*{margin:0;padding:0;color:#f1f1f1;font-family:'Quantico', sans-serif;font-size:16px;border:none;list-style-type:none;outline:none;scroll-behavior:smooth;-webkit-user-select:none;-ms-user-select:none;user-select:none}button{cursor:pointer}body{background:#181818}a{color:#bc69f7;font-size:.8rem}em{color:#480577}.App{display:flex;flex-direction:column;justify-content:center;align-items:center}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@media (min-width: 676px){.GameList{justify-content:center;flex-direction:row;flex-wrap:wrap}.GameList__item{display:flex;flex-direction:row;margin:10px 60px;width:250px;box-shadow:-2px 5px 6px .2px #0000003a;text-align:center}.GameList__item--anim{position:relative;left:0;display:flex;align-items:center;width:100%;transform:translate(20px, 25px);opacity:.75}}

